.app{

    height: 100vh;

    .sections{

        width: 100%;
        height: calc(100vh - 70px);
        background-color:salmon;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        // Snap scrolling
        scroll-snap-type: y mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
         
    }

}